.root {
  display: block;
  text-decoration: none;
  color: inherit; }
  .root + .root {
    margin-top: 1rem; }

.imageContainer {
  background-color: #efe7ee; }
  .root:hover .imageContainer {
    background-color: #d1b9cc; }

.image {
  display: block;
  width: 100%;
  mix-blend-mode: multiply; }

.content {
  padding: 1rem;
  background-color: white; }

.heading {
  margin: 0;
  font-size: 1.25rem;
  font-weight: normal;
  font-family: "Oswald", Helvetica, Arial, sans-serif;
  line-height: 1;
  text-decoration: none; }
  .root:hover .heading {
    text-decoration: underline; }

.text {
  margin: 1rem 0 0;
  text-decoration: none; }
  .root:hover .text {
    text-decoration: underline; }



